* {
  box-sizing: border-box; }

.flex {
  display: flex; }
  .flex.columns-direction {
    flex-direction: column; }
  .flex.align-start {
    align-items: flex-start; }
  .flex.align-center {
    align-items: center; }
  .flex.align-end {
    align-items: flex-end; }
  .flex.justify-start {
    justify-content: flex-start; }
  .flex.justify-center {
    justify-content: center; }
  .flex.justify-end {
    justify-content: flex-end; }
  .flex.space-between {
    justify-content: space-between; }
  .flex.wrap {
    flex-wrap: wrap; }
  .flex .fit-container {
    flex: 1 1 auto; }
    .flex .fit-container.no-basis {
      flex-basis: 0; }
  .flex .self-start {
    align-self: flex-start; }
  .flex .self-end {
    align-self: flex-end; }
  .flex.pull-right, .flex > .pull-right {
    margin-left: auto;
    float: unset !important; }

@font-face {
  font-family: 'icomoon';
  src: url(../assets/fonts/icomoon.eot);
  src: url(../assets/fonts/icomoon.eot#iefix) format("embedded-opentype"), url(../assets/fonts/icomoon.ttf) format("truetype"), url(../assets/fonts/icomoon.woff) format("woff"), url(../assets/fonts/icomoon.svg#icomoon) format("svg");
  font-weight: normal;
  font-style: normal; }

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-keyboard_arrow_up:before {
  content: "\e900"; }

.CodeMirror {
  border: 1px solid rgba(0, 0, 20, 0.1);
  border-radius: 2px;
  transition: border linear 50ms; }

.CodeMirror-focused {
  border-color: #0D99F2; }

.collapse {
  transition: 0.4s;
  animation-timing-function: ease-in-out;
  transform-style: preserve-3d;
  transform-origin: top;
  display: block;
  width: 100%;
  margin: 10px auto;
  padding-bottom: 0 !important;
  cursor: pointer;
  background: #FFF;
  border-radius: 4px;
  box-shadow: 0 3px 2px 1px rgba(0, 0, 0, 0.3); }
  .collapse__title {
    width: 100%;
    height: 50px;
    position: relative; }
    .collapse__title span {
      font-size: 20px;
      padding: 0 20px; }
    .collapse__title > .field {
      position: relative;
      margin: 0 0 0 auto; }
  .collapse__enable {
    position: absolute;
    top: 0;
    right: 50px;
    height: 50px;
    z-index: 10; }
  .collapse__arrow {
    margin-left: auto;
    width: 50px;
    height: 50px;
    font-size: 20px; }
    .collapse__arrow:before {
      float: right;
      transition: all 0.3s; }
  .collapse__content {
    transition: 0.4s;
    animation-timing-function: ease-in-out;
    transform-style: preserve-3d;
    transform-origin: top;
    position: relative;
    display: block;
    width: 100%;
    overflow: hidden;
    cursor: pointer;
    background: #FFF;
    border-radius: 4px; }
    .collapse__content > div {
      padding: 0 20px 20px; }
  .collapse > input[type=checkbox] {
    display: none; }
    .collapse > input[type=checkbox]:not(:checked) ~ .collapse__content {
      max-height: 0 !important; }
    .collapse > input[type=checkbox]:not(:checked) ~ .collapse__title .collapse__arrow:before {
      transform: rotate(180deg); }

.bar {
  margin: 16px 0;
  padding: 12px 20px;
  border-radius: 4px;
  line-height: 24px;
  font-size: 16px;
  font-weight: 400; }
  .bar--info {
    color: #0c5460;
    background-color: #d1ecf1;
    border-color: #bee5eb; }
  .bar--warning {
    color: #856404;
    background-color: #fff3cd;
    border-color: #ffeeba; }
  .bar--secondary {
    color: #383d41;
    background-color: #e2e3e5;
    border-color: #d6d8db; }

#settings-reCAPTCHACallExample-field,
#settings-googleMapsCallExample-field,
#settings-googleAdwordsConversionCallExample-field,
#settings-linkedinCallExample-field,
#settings-twitterCallExample-field,
#settings-vimeoCallExample-field,
#settings-youtubeCallExample-field {
  margin-top: 0; }
  #settings-reCAPTCHACallExample-field .CodeMirror,
  #settings-googleMapsCallExample-field .CodeMirror,
  #settings-googleAdwordsConversionCallExample-field .CodeMirror,
  #settings-linkedinCallExample-field .CodeMirror,
  #settings-twitterCallExample-field .CodeMirror,
  #settings-vimeoCallExample-field .CodeMirror,
  #settings-youtubeCallExample-field .CodeMirror {
